header {

    max-width: 1200px;
    margin: 0 auto;

}

.header_container {

    height: 75px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    background-color: #07064f;

    padding: 0 20px;

}

.header_logo {

    color: white;
    font-weight: bold;
    font-size: 24px;

    cursor: pointer;
    transition: all .2s linear;

    width: 120px;

    text-decoration: none;
    text-align: center;

}

.header_logo:hover {

    opacity: 0.7;

}

.auth_wrapper {

    position: relative;

}

.auth_wrapper button {

    width: 70px;

    background-color: transparent;
    border: none;
    padding: 0;

    color: white;
    font-weight: bolder;
    font-size: 18px;

    cursor: pointer;
    transition: all .2s linear;

}

.auth_wrapper button:hover {

    opacity: 0.8;
    text-decoration: underline;

}

@media screen and (max-width: 1280px){

    header {
        max-width: 100%;
    }

}

@media screen and (max-width: 750px) {

    .auth_wrapper button {

        width: 55px;
        font-size: 15px;

    }

}